import { PublicKey } from "@solana/web3.js";
import { vecU8 } from "@project-serum/borsh";
import * as BufferLayout from "buffer-layout";
import { config } from "dotenv";
config();

export const devnetUrl = "https://api.devnet.solana.com";
export const localnetUrl = "http://127.0.0.1:8899";
export const providerUrl = "https://www.sollet.io";
export const explorerLink = "https://solscan.io";
export const fee = Number(process.env.REACT_APP_RELAYER_FEE) || 50;
export const network = process.env.REACT_APP_NETWORK || "localnet";
export const coingeckoApi = "https://api.coingecko.com/api/v3";
export const relayerUrl = process.env.REACT_APP_RELAYER_URL || "http://localhost:5000";

export const METASOL_PROGRAM_ID = new PublicKey(
    'ErSvTPUJY2PhBp64qGEvFQALKdowYVFi6wjPzu8WdrGx',
);

export const METASOL_PUBLIC_KEY = new PublicKey(
    'DfL4bGjzMx7bgY7MDKEPy6oNDq6zfthomaLXtTh8eKh2',
);

export const FEE_PAYER_PUBLIC_KEY = new PublicKey(
    '7MR5acnJ5vAaJXd6DNndeLvuDLNvBYEYTBs27sWagGVy',
);

const uint64Layout = (property = 'uint64') => {
    return BufferLayout.blob(8, property);
}

export const processTxLayout = BufferLayout.struct([
    BufferLayout.u8('instruction'),
    uint64Layout('fee'),
    uint64Layout('expiry_block'),
    BufferLayout.u8('required_accounts_length'),
    vecU8('instruction_data'),
]);

export const transferCheckedLayout = BufferLayout.struct([
    BufferLayout.u8('instruction'),
    uint64Layout('amount'),
    BufferLayout.u8('decimals'),
]);
