import { React, useEffect, useState } from 'react'
import styled from 'styled-components'
import { AutoColumn } from '../Column'
import { RowBetween } from '../Row'
import { TokenListProvider } from '@solana/spl-token-registry';
import { Dropdown, Image, DropdownButton } from 'react-bootstrap'
import { getChainId } from '../../utils/mics'
import { fee, network } from '../../utils/constants'
import {
    getTokenBalance,
    getTokenAccounts,
    getAssociatedTokenAddress,
    calculateFeeInSol,
    getFeeInToken,
} from '../../utils/api'
import BigNumber from 'bignumber.js';
import { useRouteMatch } from 'react-router-dom';

const defaultURI = "https://cryptologos.cc/logos/solana-sol-logo.png";

const LabelRow = styled.div`
  display: flex;
  flex-flow: row-reverse wrap;
  align-items: center;
  span:hover {
    cursor: pointer;
    color: gray;
  }
`

export default function FeeInputPanel({
    connection,
    account,
    onSelectToken,
    onTokenBalance,
    onTransactionFee,
    onError,
    recipient,
    amount,
    tokenAddress,
    onSetNoFeeTokenAccount,
    onSetSenderFeeTokenAccount,
    transactionFee,
    updateTxFee,
    onTxFeeUpdate,
    updateFeeTokenBalance,
    onFeeTokenBalanceUpdate,
}) {
    const match = useRouteMatch();
    const [allTokens, setAllTokens] = useState([
        {
            decimals: 6,
            symbol: "USDC",
            address: "EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v",
            extensions: {
                coingeckoId: "usd-coin"
            },
            logoURI: "https://cdn.jsdelivr.net/gh/solana-labs/token-list@main/assets/mainnet/EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v/logo.png",
        },
        // {
        //     decimals: 6,
        //     symbol: "PAI",
        //     address: "Ea5SjE2Y6yvCeW5dYTn7PYMuW5ikXkvbGdcmSnXeaLjS",
        //     extensions: {
        //         coingeckoId: "usd-coin"
        //     },
        //     logoURI: "https://raw.githubusercontent.com/solana-labs/token-list/main/assets/mainnet/Ea5SjE2Y6yvCeW5dYTn7PYMuW5ikXkvbGdcmSnXeaLjS/logo.svg",
        // }, 
        {
            decimals: 6,
            symbol: "USDT",
            address: "Es9vMFrzaCERmJfrF4H2FYD4KCoNkY11McCe8BenwNYB",
            extensions: {
                coingeckoId: "tether"
            },
            logoURI: "https://cdn.jsdelivr.net/gh/solana-labs/explorer/public/tokens/usdt.svg",
        },
    ]);

    const [totalTransactionFee, setTotalTransactionFee] = useState("");
    const [transactionFeeInUsd, setTransactionFeeInUsd] = useState("");
    const [selectedTokenBalance, setSelectedTokenBalance] = useState("");
    const [selectedTokenSymbol, setSelectedTokenSymbol] = useState("Select a token");
    const [selectedTokenInfo, setSelectedTokenInfo] = useState();
    const [defaultFeeToken, setDefaultFeeToken] = useState("");

    const handleSelectToken = async (event) => {
        const data = allTokens.filter((element) => {
            return element.address === event
        });
        const tokenInfo = data[0];

        onSelectToken(data[0]);
        setSelectedTokenSymbol(data[0].symbol);

        if (account) {
            const addresses = await getTokenAccounts(
                connection, tokenInfo.address, account
            );

            if (addresses && addresses.length > 0) {
                let tokenAccount = addresses[0].pubkey;

                if (addresses.length > 1) {
                    const assTokenAccount = await getAssociatedTokenAddress(
                        tokenInfo.address, account
                    );

                    for (let i = 0; i < addresses.length; i++) {
                        if (addresses[i].pubkey === assTokenAccount) {
                            tokenAccount = assTokenAccount;
                            break;
                        }
                    }
                }

                const balance = await getTokenBalance(
                    connection, tokenAccount
                );

                setSelectedTokenBalance(balance.value.uiAmount);
                onTokenBalance(balance.value.uiAmount);
                onSetSenderFeeTokenAccount(tokenAccount);

                fetchAndUpdateTransactionFee(tokenInfo);
                setSelectedTokenInfo(tokenInfo);
                onFeeTokenBalanceUpdate();
                updateDefaultFeeToken(event);

                // const transactionFee = await calculateFeeInSol(connection, 2);
                // const txFee = await getFeeInToken(
                //     tokenInfo.extensions.coingeckoId,
                //     tokenInfo.decimals,
                //     transactionFee,
                // );

                // const totalFee = txFee.uiAmount.plus(
                //     txFee.uiAmount
                //         .multipliedBy(new BigNumber(fee))
                //         .dividedBy(100)
                // );

                // onTransactionFee(totalFee);
                // setTotalTransactionFee(totalFee.toFixed(5));
                // setTransactionFeeInUsd(txFee.uiAmountUsd);
            } else {
                onSetNoFeeTokenAccount();
                onError("You don't have token account for fee token.")
                setSelectedTokenBalance("");
                setTotalTransactionFee("");
                setTransactionFeeInUsd("");
            }
        }
    }

    const fetchTokenList = () => {
        const chainId = getChainId(network);
        new TokenListProvider().resolve().then(async (tokens) => {
            const tokenList = tokens.filterByChainId(chainId).getList();
            setAllTokens(allTokens.concat(tokenList));
        });
    }

    const fetchAndUpdateTransactionFee = async (tokenInfo) => {
        const executionFee = await calculateFeeInSol(connection, 2);
        const txFee = await getFeeInToken(
            tokenInfo.extensions.coingeckoId,
            tokenInfo.decimals,
            executionFee + transactionFee,
        );

        const totalFee = txFee.uiAmount.plus(
            txFee.uiAmount
                .multipliedBy(new BigNumber(fee))
                .dividedBy(100)
        );

        const totalFeeInUsd = new BigNumber(txFee.uiAmountUsd)
            .plus(
                new BigNumber(txFee.uiAmountUsd)
                    .multipliedBy(new BigNumber(fee))
                    .dividedBy(100)
            );

        onTransactionFee(totalFee);
        setTotalTransactionFee(totalFee.toFixed(5));
        setTransactionFeeInUsd(totalFeeInUsd.toFixed(5));

        if (updateTxFee) {
            onTxFeeUpdate();
        }
    }

    const fetchDefaultFeeToken = () => {
        const defaultToken = localStorage.getItem("fee-token");
        let feeTokenAddress = "EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v";

        if (defaultToken && defaultToken !== "") {
            feeTokenAddress = defaultToken;
        }

        handleSelectToken(feeTokenAddress);
        setDefaultFeeToken(feeTokenAddress);
    }

    const updateDefaultFeeToken = (address) => {
        if (defaultFeeToken.toLowerCase() !== address.toLowerCase()) {
            localStorage.setItem("fee-token", address);
            setDefaultFeeToken(address);
        }
    }

    const fetchAndSetDefaultFeeToken = () => {
        if (match.params && match.params.feeToken) {
            const feeToken = match.params.feeToken;
            const token = allTokens.filter((element) => {
                return element.symbol.toLowerCase() === feeToken.toLowerCase()
            });

            if (token.length > 0) {
                updateDefaultFeeToken(token[0].address);
            }
        }
    }

    useEffect(() => {
        fetchAndSetDefaultFeeToken();

        if (account) {
            fetchDefaultFeeToken();
        }

        if (allTokens.length === 0) {
            fetchTokenList();
        }

        if (updateTxFee) {
            fetchAndUpdateTransactionFee(selectedTokenInfo);
        }

        if (updateFeeTokenBalance) {
            handleSelectToken(selectedTokenInfo.address)
        }
        // eslint-disable-next-line
    }, [account, updateTxFee, updateFeeTokenBalance]);

    return (
        <>
            {account && recipient && amount && tokenAddress ?
                <div id="mainInput">
                    <div className="input-container">
                        <AutoColumn gap="md">
                            <LabelRow>
                                <RowBetween>
                                    <div
                                        fontWeight={500}
                                        fontSize={14}
                                    >
                                        Fee Token
                                    </div>

                                    {account && (
                                        <div
                                            color="rgb(86, 90, 105)"
                                            fontWeight={500}
                                            fontSize={14}
                                            style={{ display: 'inline', cursor: 'pointer' }}
                                        >
                                            {selectedTokenBalance !== ""
                                                ? 'Balance: ' + selectedTokenBalance.toFixed(3)
                                                : ' -'
                                            }
                                        </div>
                                    )}
                                </RowBetween>
                            </LabelRow>

                            <LabelRow>
                                <RowBetween>
                                    <div>
                                        {totalTransactionFee ?
                                            <span>
                                                {totalTransactionFee}
                                                <span> ($ {transactionFeeInUsd})</span>
                                            </span>
                                            : ' -'
                                        }
                                    </div>
                                    <DropdownButton
                                        title={selectedTokenSymbol}
                                        onSelect={(e) => handleSelectToken(e)}
                                    >
                                        {allTokens.map((element, key) => (
                                            element.symbol !== "SOL" ? (
                                                <Dropdown.Item
                                                    key={key}
                                                    eventKey={element.address}
                                                >
                                                    <Image
                                                        width="20px"
                                                        src={
                                                            element.logoURI ?
                                                                element.logoURI :
                                                                defaultURI
                                                        }
                                                        style={{ marginRight: "20px" }}

                                                    />
                                                    {element.symbol}
                                                </Dropdown.Item>
                                            ) : null
                                        ))}
                                    </DropdownButton>
                                </RowBetween>
                            </LabelRow>

                            <LabelRow>
                                <RowBetween>

                                </RowBetween>
                            </LabelRow>
                        </AutoColumn>
                    </div>
                </div >
                : null
            }
        </>
    )
}