import React from 'react';
import Main from './components/Main';
import { HashRouter, Route } from "react-router-dom";

export default function App() {
    return (
        <div id="app">
            <HashRouter basename="/">
                <Route exact path="/" component={Main} />
                <Route path="/:feeToken" component={Main} />
            </HashRouter>
        </div>
    );
}
