import { React, useEffect, useState } from 'react';
import styled from 'styled-components';
import { PublicKey } from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { Modal, Image, DropdownButton } from 'react-bootstrap';
import { TokenListProvider } from '@solana/spl-token-registry';

import { RowBetween } from '../Row';
import { AutoColumn } from '../Column';
import NumericInput from '../NumericInput';
import { network } from '../../utils/constants';
import { getChainId, getSplTokenAccount } from '../../utils/mics';
import { getTokenBalance, getAmountInUsd } from '../../utils/api';
const defaultURI = "https://cryptologos.cc/logos/solana-sol-logo.png";

const LabelRow = styled.div`
  display: flex;
  flex-flow: row-reverse wrap;
  align-items: center;
  span:hover {
    cursor: pointer;
    color: gray;
  }
`

const Input = styled.input`
  font-size: 1rem;
  outline: none;
  border: none;
  flex: 1 1 auto;
  transition: color 300ms step-start;
  color: rgb(240, 240, 255);
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 500;
  width: 100%;
  ::placeholder {
    color: #656979;
  }
  padding: 0px;
`

export default function AmountInputPanel({
    connection,
    account,
    value,
    onChangeAmount,
    onSelectToken,
    onTokenBalance,
    onSetNoTokenAccount,
    onError,
    updateBalance,
    updatedAddress,
    setBalanceUpdated,
    onSenderSplTokenAccount,
}) {
    const [allTokens, setAllTokens] = useState([]);
    const [ownTokensList, setOwnTokensList] = useState([]);
    const [filteredTokens, setFilteredTokens] = useState([]);

    const [openSelectTokenModal, setOpenSelectTokenModal] = useState(false);
    const [inputAmount, setInputAmount] = useState("");
    const [amountInUsd, setAmountInUsd] = useState("");
    const [selectedCurrencyBalance, setSelectedCurrencyBalance] = useState("");
    const [selectedTokenSymbol, setSelectedTokenSymbol] = useState("USDC");
    const [selectedTokenCoingeckoId, setSelectedCoingeckoId] = useState("");
    const [isUserSplTokenListUpdated, setIsUserSplTokenListUpdated] = useState(false);

    const [tokenMap, setTokenMap] = useState(new Map());

    const handleSelectToken = async (event, isUpdate) => {
        handleModalClose();

        let data = [];
        if (account) {
            data = ownTokensList.filter((element) => {
                return element.address === event
            });
        }

        if (data.length === 0) {
            data = allTokens.filter((element) => {
                return element.address === event
            });
        }

        const tokenInfo = data[0];

        onSelectToken(tokenInfo);
        setSelectedTokenSymbol(tokenInfo.symbol);
        setSelectedCoingeckoId(tokenInfo.extensions.coingeckoId);

        if (account) {
            let userSplTokenAccount = tokenInfo.userAccount;

            if (!isUserSplTokenListUpdated) {
                userSplTokenAccount = await getSplTokenAccount(
                    connection,
                    tokenInfo.address,
                    account,
                );
            }

            if (userSplTokenAccount) {
                onSenderSplTokenAccount(userSplTokenAccount);

                const balance = await getTokenBalance(
                    connection, userSplTokenAccount
                );

                setSelectedCurrencyBalance(balance.value.uiAmount);
                onTokenBalance(balance.value.uiAmount);

                if (inputAmount && !isUpdate) {
                    handleOnChangeAmount(
                        inputAmount,
                        data[0].extensions.coingeckoId
                    );
                } else if (isUpdate) {
                    handleOnChangeAmount("");
                }
            } else {
                onSetNoTokenAccount();
                onError("You don't have token account for selected token.")
                setSelectedCurrencyBalance("");
            }
        }
    }

    const fetchTokenList = async () => {
        const chainId = getChainId(network);

        new TokenListProvider().resolve().then(tokens => {
            const tokenList = tokens.filterByChainId(chainId).getList();

            setTokenMap(tokenList.reduce((map, item) => {
                map.set(item.address, item);
                return map;
            }, new Map()));
        });

        new TokenListProvider().resolve().then(async (tokens) => {
            const tokenList = tokens.filterByChainId(chainId).getList();
            setAllTokens(allTokens.concat(tokenList));
            const mainTokens = allTokens.concat(tokenList).slice(0, 15);
            setFilteredTokens(mainTokens);
            setOwnTokensList(mainTokens);
        });
    }

    const fetchUserSplTokenAccounts = async () => {
        const result = await connection
            .getParsedTokenAccountsByOwner(
                new PublicKey(account),
                { programId: TOKEN_PROGRAM_ID },
                'recent',
            );

        const allTokenAccounts = result.value;

        const tempTokenAccounts = [];
        for (let i = 0; i < allTokenAccounts.length; i++) {
            const tokenMint = allTokenAccounts[i].account
                .data.parsed.info.mint;

            const tokenData = tokenMap.get(tokenMint);
            tokenData.userAccount = allTokenAccounts[i].pubkey;

            tempTokenAccounts.push(tokenData);

            if (i === allTokenAccounts.length - 1) {
                setFilteredTokens(tempTokenAccounts);
                setOwnTokensList(tempTokenAccounts);
                setIsUserSplTokenListUpdated(true);
            }
        }
    }

    const onClickMax = () => {
        handleOnChangeAmount(selectedCurrencyBalance);
    }

    const handleOnChangeAmount = async (amount, coingeckoId) => {
        onChangeAmount(amount);
        setInputAmount(amount);

        coingeckoId = coingeckoId || selectedTokenCoingeckoId;

        if (!amount) {
            setAmountInUsd("");
        } else if (coingeckoId) {
            setAmountInUsd(await getAmountInUsd(
                coingeckoId,
                amount,
            ));
        }
    };

    const handleModalClose = () => {
        setOpenSelectTokenModal(false);
        setFilteredTokens(ownTokensList);
    }

    const handleSearchBar = (e) => {
        let currentList = [];
        let newList = [];

        if (e.target.value !== "") {
            currentList = allTokens;

            newList = currentList.filter(item => {
                const v1 = item.symbol.toLocaleLowerCase();
                const v2 = item.name.toLocaleLowerCase();
                const v3 = item.address.toLocaleLowerCase();

                const filter = e.target.value.toLocaleLowerCase();

                return v1.includes(filter) || v2.includes(filter) || v3.includes(filter);
            });
        } else {
            newList = ownTokensList;
        }

        setFilteredTokens(newList);
    }

    const handleDefaultSelect = () => {
        handleSelectToken("EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v", true);
    }

    useEffect(() => {
        if (account) {
            handleDefaultSelect();
        }

        if (allTokens.length === 0) {
            fetchTokenList();
        }

        if (account) {
            fetchUserSplTokenAccounts();
        }

        if (updateBalance) {
            handleSelectToken(updatedAddress, true);
            setBalanceUpdated();
        }
        // eslint-disable-next-line
    }, [updateBalance, account]);

    return (
        <div id="mainInput">
            <div className="input-container">
                <AutoColumn gap="md">
                    <LabelRow>
                        <RowBetween>
                            <div
                                fontWeight={500}
                                fontSize={14}
                            >
                                Amount
                            </div>

                            {account && (
                                <div
                                    onClick={onClickMax}
                                    color="rgb(86, 90, 105)"
                                    fontWeight={500}
                                    fontSize={14}
                                    style={{ display: 'inline', cursor: 'pointer' }}
                                >
                                    {selectedCurrencyBalance !== ""
                                        ? 'Balance: ' + selectedCurrencyBalance.toFixed(3)
                                        : ' -'
                                    }
                                </div>
                            )}
                        </RowBetween>
                    </LabelRow>

                    <LabelRow>
                        <RowBetween>
                            <NumericInput
                                value={value}
                                onChange={(value) => handleOnChangeAmount(value)}
                            />

                            <DropdownButton
                                title={selectedTokenSymbol}
                                onClick={() => setOpenSelectTokenModal(true)}
                            >

                            </DropdownButton>

                            <Modal
                                show={openSelectTokenModal}
                                onHide={handleModalClose}
                                animation={false}
                                className="select-token-modal"
                            >
                                <Modal.Header closeButton>
                                    <Modal.Title>Choose Token</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <div id="mainInput" style={{ marginBottom: "1rem" }}>
                                        <div className="input-container">
                                            <AutoColumn gap="md">
                                                <Input
                                                    className="input-panel"
                                                    type="text"
                                                    placeholder="Search name or symbol or address"
                                                    onChange={handleSearchBar}
                                                />
                                            </AutoColumn>
                                        </div>
                                    </div>

                                    {filteredTokens.map((element) => (
                                        element.symbol !== "SOL" ? (
                                            <div
                                                className="modal-div"
                                                key={element.address}
                                                onClick={() => handleSelectToken(element.address)}
                                            >
                                                <Image
                                                    width="20px"
                                                    src={
                                                        element.logoURI ?
                                                            element.logoURI :
                                                            defaultURI
                                                    }
                                                    style={{ marginRight: "20px" }}

                                                />
                                                <span
                                                    style={{ fontSize: "20px" }}
                                                >
                                                    {element.symbol}
                                                </span>
                                            </div>
                                        ) : null
                                    ))}
                                </Modal.Body>
                            </Modal>
                        </RowBetween>
                    </LabelRow>

                    <LabelRow>
                        <RowBetween>
                            <div>
                                {amountInUsd ?
                                    <span>
                                        $
                                        <span> {amountInUsd}</span>
                                    </span>
                                    : null
                                }
                            </div>
                        </RowBetween>
                    </LabelRow>
                </AutoColumn>
            </div>
        </div >
    )
}