import {
    PublicKey,
    SYSVAR_CLOCK_PUBKEY,
    Transaction,
    TransactionInstruction,
} from '@solana/web3.js';
import ReactGA from 'react-ga';
import BigNumber from 'bignumber.js';
import {
    FEE_PAYER_PUBLIC_KEY,
    METASOL_PROGRAM_ID,
    processTxLayout,
    METASOL_PUBLIC_KEY,
    transferCheckedLayout,
} from "./constants";
import { u64 } from './uint64';
import {
    sendToRelayer,
    getAssociatedTokenAddress,
    calculateFeeInSol,
    getFeeInToken,
    getTokenAccounts,
    getAssociatedAccountCreationCost,
    createAssociatedTokenAccountInstruction,
} from './api';
import {
    TOKEN_PROGRAM_ID,
} from '@solana/spl-token';

export const getChainId = (network) => {
    let chainId = 0;

    switch (network) {
        case "mainnet-beta":
            chainId = 101;
            break;
        case "testnet":
            chainId = 102;
            break;
        case "devnet":
            chainId = 103;
            break;
        default:
            chainId = 102;
            break;
    }

    return chainId;
}

export const signAndSendTransaction = (
    connection,
    solletWallet,
    recipient,
    transferAmount,
    selectedTokenData,
    selectedFeeTokenData,
    senderSplTokenAccount,
    senderFeeTokenAccount,
) => {
    return new Promise(async (resolve, reject) => {
        try {
            let recipientSplAccount = await
                getSplTokenAccount(
                    connection,
                    selectedTokenData.address,
                    recipient,
                );

            // if (await isSplAccountExists(
            //     connection,
            //     recipientSplAccount,
            // )) {
            const mintPublicKey = new PublicKey(
                selectedTokenData.address
            );

            const amount = Number(
                new BigNumber(transferAmount).times(
                    new BigNumber(10).exponentiatedBy(
                        new BigNumber(selectedTokenData.decimals)
                    )
                )
            );

            const recentBlock = await connection
                .getRecentBlockhash('recent');

            const tempTransaction = new Transaction({
                feePayer: FEE_PAYER_PUBLIC_KEY,
                recentBlockhash: recentBlock.blockhash
            });

            let accountCreationCost = 0;
            if (!recipientSplAccount || recipientSplAccount.toString() === "") {
                recipientSplAccount = await getAssociatedTokenAddress(
                    selectedTokenData.address, recipient,
                );

                accountCreationCost += await
                    getAssociatedAccountCreationCost(connection);

                const recipientSplAccountInstruction =
                    createAssociatedTokenAccountInstruction(
                        new PublicKey(selectedTokenData.address), // Token mint key
                        recipientSplAccount, // Token account
                        recipient, // Account Owner
                        FEE_PAYER_PUBLIC_KEY, // Fee Payer
                    );

                tempTransaction.add(recipientSplAccountInstruction);
            }

            const transferInstruction =
                createTransferCheckedInstruction(
                    senderSplTokenAccount,
                    mintPublicKey,
                    recipientSplAccount,
                    solletWallet.publicKey,
                    [],
                    amount,
                    selectedTokenData.decimals
                );

            const transaction = await createMetasolTransaction(
                connection,
                tempTransaction,
                transferInstruction,
                solletWallet.publicKey,
                selectedFeeTokenData,
                senderFeeTokenAccount,
                accountCreationCost,
            );

            const signedTx = await solletWallet
                .signTransaction(transaction);

            const bufferTx = signedTx.serialize({
                verifySignatures: false
            });

            const result = await sendToRelayer(bufferTx);
            resolve(result);

            ReactGA.event({
                category: 'Transfer',
                action: 'Executed',
                label: amount.toString(),
            });
            // } else {
            //     reject(new Error("Receiver doesn't have token account !!"));
            // }
        } catch (error) {
            console.log(error)
            reject(error);
        }
    });
}

const createMetasolTransaction = async (
    connection,
    transaction,
    txInstruction,
    senderAuthority,
    feeSplToken,
    senderFeeTokenAccount,
    accountCreationCost,
) => {
    const feeSplTokenAddress = feeSplToken.address;
    const feePayerSplAccount = await getAssociatedTokenAddress(
        feeSplTokenAddress, FEE_PAYER_PUBLIC_KEY.toString(),
    );

    const feeTokenPublicKey = new PublicKey(feeSplTokenAddress);

    const metasolKeys = [
        { pubkey: txInstruction.programId, isSigner: false, isWritable: false },
        { pubkey: METASOL_PUBLIC_KEY, isSigner: false, isWritable: true },

        { pubkey: SYSVAR_CLOCK_PUBKEY, isSigner: false, isWritable: false },

        { pubkey: senderFeeTokenAccount, isSigner: false, isWritable: true },
        { pubkey: senderAuthority, isSigner: false, isWritable: false },
        { pubkey: feePayerSplAccount, isSigner: false, isWritable: true },

        { pubkey: feeTokenPublicKey, isSigner: false, isWritable: false },
        { pubkey: TOKEN_PROGRAM_ID, isSigner: false, isWritable: false },
    ];

    const keys = txInstruction.keys.concat(metasolKeys);

    const transactionFee = await calculateFeeInSol(connection, 2);

    const txFee = await getFeeInToken(
        feeSplToken.extensions.coingeckoId,
        feeSplToken.decimals,
        transactionFee + accountCreationCost,
    );

    const slot = await connection.getSlot('recent');

    const data = Buffer.alloc(48);

    processTxLayout.encode(
        {
            instruction: 1,
            fee: new u64(txFee.amount).toBuffer(),
            expiry_block: new u64(slot + 300).toBuffer(),
            required_accounts_length: txInstruction.keys.length,
            instruction_data: txInstruction.data,
        },
        data,
    );

    // if (
    //     !await isSplAccountExists(
    //         connection,
    //         feePayerSplAccount,
    //     )
    // ) {
    //     const feePayerAccountInstruction =
    //         createAssociatedTokenAccountInstruction(
    //             feeTokenPublicKey, // Token mint key
    //             feePayerSplAccount, // Token account
    //             FEE_PAYER_PUBLIC_KEY, // Owner
    //             FEE_PAYER_PUBLIC_KEY, // Payer
    //         );

    //     transaction.add(feePayerAccountInstruction);
    // }

    const processTxInstruction = new TransactionInstruction({
        keys,
        programId: METASOL_PROGRAM_ID,
        data,
    });

    transaction.add(processTxInstruction);

    return transaction;
}

function createTransferCheckedInstruction(
    source,
    mint,
    destination,
    owner,
    multiSigners,
    amount,
    decimals,
) {
    const data = Buffer.alloc(transferCheckedLayout.span);

    transferCheckedLayout.encode(
        {
            instruction: 12, // TransferChecked instruction
            amount: new u64(amount).toBuffer(),
            decimals,
        },
        data,
    );

    let keys = [
        { pubkey: source, isSigner: false, isWritable: true },
        { pubkey: mint, isSigner: false, isWritable: false },
        { pubkey: destination, isSigner: false, isWritable: true },
    ];
    if (multiSigners.length === 0) {
        keys.push({
            pubkey: owner,
            isSigner: true,
            isWritable: false,
        });
    } else {
        keys.push({ pubkey: owner, isSigner: false, isWritable: false });
        multiSigners.forEach((signer) =>
            keys.push({
                pubkey: signer.publicKey,
                isSigner: true,
                isWritable: false,
            }),
        );
    }

    return new TransactionInstruction({
        keys,
        programId: TOKEN_PROGRAM_ID,
        data,
    });
}

export const getSplTokenAccount = (connection, mint, authority) => {
    return new Promise(async (resolve, reject) => {
        try {
            let userSplTokenAccount;
            const addresses = await getTokenAccounts(
                connection, mint, authority
            );

            if (addresses && addresses.length > 0) {
                userSplTokenAccount = addresses[0].pubkey;

                if (addresses.length > 1) {
                    const associatedTokenAddr = await
                        getAssociatedTokenAddress(mint, authority);

                    for (let i = 0; i < addresses.length; i++) {
                        if (addresses[i].pubkey === associatedTokenAddr) {
                            userSplTokenAccount = associatedTokenAddr;
                            break;
                        }
                    }
                }
            }

            resolve(userSplTokenAccount);
        } catch (error) {
            reject(error)
        }
    })
}

