import { useEffect, useState } from "react";
import styled from 'styled-components';

const BetaMessage = styled.div`
  cursor: pointer;
  flex: 1 0 auto;
  align-items: center;
  position: relative;
  padding: 0.5rem 1rem;
  padding-right: 2rem;
  border-radius: 2rem;
  font-size: 0.75rem;
  line-height: 1rem;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  &:after {
    content: '';
    top: 0.5rem;
    right: 1rem;
    position: absolute;
  }
`

export default function BetaWarning() {
    const [showBetaMessage, setShowBetaMessage] = useState(true);

    const handleDisableBetaMsg = () => {
        localStorage.setItem("beta-disabled", true);
        setShowBetaMessage(false);
    }

    useEffect(() => {
        const result = localStorage.getItem("beta-disabled");
        if (Boolean(result)) {
            setShowBetaMessage(false);
        }
    }, []);

    return (
        <div id="mainInput" style={{ marginBottom: "1rem", border: "0px" }}>
            {showBetaMessage && (
                <BetaMessage className="warning-box" onClick={handleDisableBetaMsg}>
                    <span role="img" aria-label="warning">
                        
                    </span>{' '}
                    <span style={{ marginLeft: "0.3rem" }}>
                        This project is unaudited. Use at your own risk.
                    </span>
                </BetaMessage>
            )}
        </div>
    )
}